.addition {
  padding-top: 25px;
  padding-bottom: 35px;
}
.addition__head {
  margin-bottom: 36px;
  position: relative;
}
.addition__head::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: 2px;
  background: green;
  z-index: -1;
}
@media (min-width: 768px) {
  .addition__head {
    margin-bottom: 80px;
  }
}
.addition__title {
  display: inline-block;
  color: var(--color-black-main);
  font-size: 24px;
  line-height: 1.5;
  text-align: left;
  background: var(--color-white-main);
  padding-left: 20px;
  padding-right: 22px;
  z-index: 1;
  margin: 0;
  margin-left: -20px;
}
@media (min-width: 768px) {
  .addition__title {
    font-size: 36px;
    line-height: 1.5;
    padding-left: 36px;
    padding-right: 36px;
    margin-left: 0;
  }
}
@media (min-width: 1440px) {
  .addition__title {
    padding-left: 40px;
    margin-left: -40px;
  }
}
@media (min-width: 1440px) {
  .addition__wrap {
    display: flex;
    justify-content: space-between;
  }
}
.addition__list {
  margin-bottom: 35px;
}
@media (min-width: 1440px) {
  .addition__list {
    flex: 1;
    margin-bottom: 0;
    margin-right: 80px;
  }
}
.addition__item {
  padding-top: 13px;
  padding-bottom: 19px;
  border-top: 1px solid var(--color-border-grey);
}
.addition__item:last-child {
  border-bottom: 1px solid var(--color-border-grey);
}
@media (min-width: 768px) {
  .addition__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border-left: 1px solid var(--color-border-grey);
    border-right: 1px solid var(--color-border-grey);
    padding-right: 38px;
    padding-left: 38px;
  }
}
@media (min-width: 1440px) {
  .addition__item {
    border-left: 0;
    border-right: 0;
    padding-right: 0;
    padding-left: 0;
  }
}
.addition__item-title {
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 13px;
}
@media (min-width: 768px) {
  .addition__item-title {
    font-size: 20px;
    line-height: 1.5;
    margin-bottom: 25px;
  }
}
@media (min-width: 1440px) {
  .addition__item-title {
    flex: 1;
    margin-bottom: 0;
  }
}
.addition__item-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
@media (min-width: 1440px) {
  .addition__item-wrap {
    margin-right: 38px;
    max-width: 285px;
    flex: 1;
  }
}
.addition__item-text {
  line-height: 1.5;
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .addition__item-text {
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 25px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .addition__item-text {
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  .addition__item-text + .addition__item-text {
    margin-left: 50px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .addition__item-text + .addition__item-text {
    margin-left: 0;
    margin-bottom: 0;
  }
}
.addition__btn {
  text-align: center;
}
@media (min-width: 1440px) {
  .addition__btn {
    max-width: 244px;
    min-height: 40px;
  }
}

.gift {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  min-height: 288px;
  background: url(../img/gift.jpg) no-repeat center center/cover;
  padding: 58px 60px 43px 60px;
  position: relative;
  z-index: 2;
}
.gift::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: var(--color-bg-green);
  opacity: 0.85;
  z-index: -1;
}
@media (min-width: 768px) {
  .gift {
    background: url(../img/gift768.jpg) no-repeat center center/cover;
    min-height: auto;
    flex-direction: row;
    justify-content: space-between;
    padding: 60px 124px 70px 80px;
  }
}
@media (min-width: 1440px) {
  .gift {
    background: url(../img/gift.jpg) no-repeat center center/cover;
    max-width: 245px;
    flex-direction: column;
    padding: 55px 41px 41px 41px;
  }
}
.gift__img {
  object-fit: cover;
}
@media (min-width: 1440px) {
  .gift__img {
    object-fit: contain;
    height: 100%;
  }
}
.gift__text {
  text-align: center;
  font-size: 14px;
  line-height: 1.5;
  margin-top: 48px;
  max-width: 161px;
  color: var(--color-white-main);
}
@media (min-width: 768px) {
  .gift__text {
    margin-top: 0;
  }
}